
Template.__checkName("post_domain");
Template["post_domain"] = new Template("Template.post_domain", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.lookup("url"));
  }, function() {
    return [ "\n    ", HTML.SPAN({
      "class": function() {
        return [ "post-domain ", Spacebars.mustache(view.lookup("moduleClass")) ];
      }
    }, Blaze.View("lookup:domain", function() {
      return Spacebars.mustache(view.lookup("domain"));
    })), "\n  " ];
  });
}));
