
Template.__checkName("post_comments_link");
Template["post_comments_link"] = new Template("Template.post_comments_link", (function() {
  var view = this;
  return HTML.DIV({
    "class": function() {
      return Spacebars.mustache(view.lookup("moduleClass"));
    }
  }, "\n    ", HTML.A({
    "class": "comments-link",
    href: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "getPageUrl"));
    }
  }, "\n      ", HTML.SPAN({
    "class": "comments-count"
  }, Blaze.View("lookup:commentCount", function() {
    return Spacebars.mustache(view.lookup("commentCount"));
  })), "\n      ", HTML.SPAN({
    "class": "comments-action"
  }, Blaze.View("lookup:_", function() {
    return Spacebars.mustache(view.lookup("_"), "comments_");
  })), "\n    "), "\n  ");
}));
