
Template.__checkName("post_content");
Template["post_content"] = new Template("Template.post_content", (function() {
  var view = this;
  return HTML.DIV({
    "class": function() {
      return [ "post-content ", Spacebars.mustache(view.lookup("moduleClass")) ];
    }
  }, "\n    ", Blaze._TemplateWith(function() {
    return {
      zone: Spacebars.call("postThumbnail"),
      moduleData: Spacebars.call(view.lookup("."))
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("modules"));
  }), "\n    ", HTML.DIV({
    "class": "post-info"
  }, "\n      ", Blaze._TemplateWith(function() {
    return {
      zone: Spacebars.call("postHeading"),
      zoneClass: Spacebars.call("post-heading"),
      moduleData: Spacebars.call(view.lookup("."))
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("modules"));
  }), "\n      ", Blaze._TemplateWith(function() {
    return {
      zone: Spacebars.call("postMeta"),
      zoneClass: Spacebars.call("post-meta"),
      moduleClass: Spacebars.call("post-meta-item"),
      moduleData: Spacebars.call(view.lookup("."))
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("modules"));
  }), "\n    "), "\n  ");
}));
