
Template.__checkName("post_title");
Template["post_title"] = new Template("Template.post_title", (function() {
  var view = this;
  return HTML.H3({
    "class": function() {
      return [ "post-title ", Spacebars.mustache(view.lookup("moduleClass")) ];
    }
  }, "\n    ", HTML.A({
    href: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "getLink"));
    },
    "class": "post-title",
    target: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "getLinkTarget"));
    }
  }, Blaze.View("lookup:title", function() {
    return Spacebars.mustache(view.lookup("title"));
  })), "\n  ");
}));
