
Template.__checkName("post_vote");
Template["post_vote"] = new Template("Template.post_vote", (function() {
  var view = this;
  return HTML.DIV({
    "class": function() {
      return [ "post-vote ", Spacebars.mustache(view.lookup("moduleClass")) ];
    }
  }, "\n    ", HTML.DIV({
    "class": function() {
      return [ "vote-actions ", Spacebars.mustache(view.lookup("actionsClass")) ];
    }
  }, "\n      ", HTML.A({
    "class": "vote-action upvote-link",
    href: "#",
    title: function() {
      return Spacebars.mustache(view.lookup("_"), "upvote_");
    }
  }, "\n        ", Blaze.View("lookup:icon", function() {
    return Spacebars.makeRaw(Spacebars.mustache(view.lookup("icon"), "upvote"));
  }), "\n        ", HTML.SPAN({
    "class": "sr-only"
  }, Blaze.View("lookup:_", function() {
    return Spacebars.mustache(view.lookup("_"), "upvote_");
  })), "\n      "), "\n      ", Blaze.If(function() {
    return Spacebars.call(view.lookup("enableDownvotes"));
  }, function() {
    return [ "\n        ", HTML.A({
      "class": "vote-action downvote-link",
      href: "#",
      title: function() {
        return Spacebars.mustache(view.lookup("_"), "downvote_");
      }
    }, "\n          ", Blaze.View("lookup:icon", function() {
      return Spacebars.makeRaw(Spacebars.mustache(view.lookup("icon"), "downvote"));
    }), "\n          ", HTML.SPAN({
      "class": "sr-only"
    }, Blaze.View("lookup:_", function() {
      return Spacebars.mustache(view.lookup("_"), "downvote_");
    })), "\n        "), "\n      " ];
  }), "\n    "), "\n  ");
}));
