
Template.__checkName("post_page");
Template["post_page"] = new Template("Template.post_page", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.lookup("ready"));
  }, function() {
    return [ "\n    ", Blaze.If(function() {
      return Spacebars.call(view.lookup("post"));
    }, function() {
      return [ "\n      ", Spacebars.With(function() {
        return Spacebars.call(view.lookup("post"));
      }, function() {
        return [ "\n        ", Blaze.If(function() {
          return Spacebars.call(view.lookup("canView"));
        }, function() {
          return [ "\n          ", Blaze.If(function() {
            return Spacebars.call(view.lookup("isPending"));
          }, function() {
            return [ "\n            ", HTML.DIV({
              "class": "grid"
            }, "\n              ", HTML.DIV({
              "class": "error pending-message module"
            }, "\n                ", Blaze.View("lookup:_", function() {
              return Spacebars.mustache(view.lookup("_"), "thanks_your_post_is_awaiting_approval");
            }), "\n              "), "\n            "), "\n          " ];
          }), "\n          ", HTML.DIV({
            "class": "single-post grid"
          }, "\n            ", HTML.DIV({
            "class": "posts posts-list"
          }, "\n              ", Blaze._TemplateWith(function() {
            return {
              post: Spacebars.call(view.lookup("."))
            };
          }, function() {
            return Spacebars.include(view.lookupTemplate("post_item"));
          }), "\n            "), "\n            ", Blaze.If(function() {
            return Spacebars.call(view.lookup("body"));
          }, function() {
            return [ "\n              ", Spacebars.include(view.lookupTemplate("post_body")), "\n            " ];
          }), "\n            ", Spacebars.include(view.lookupTemplate("comment_submit")), "\n            ", Spacebars.include(view.lookupTemplate("comment_list")), "\n          "), "\n        " ];
        }, function() {
          return [ "\n          ", Spacebars.include(view.lookupTemplate("no_rights")), "\n        " ];
        }), "\n      " ];
      }), "\n    " ];
    }, function() {
      return [ "\n      ", Spacebars.include(view.lookupTemplate("not_found")), "\n    " ];
    }), "\n  " ];
  }, function() {
    return [ "\n    ", HTML.DIV({
      "class": "posts-loading"
    }, Spacebars.include(view.lookupTemplate("loading"))), "\n  " ];
  });
}));
