
Template.__checkName("post_item");
Template["post_item"] = new Template("Template.post_item", (function() {
  var view = this;
  return Blaze._TemplateWith(function() {
    return {
      zone: Spacebars.call("postComponents"),
      zoneClass: Spacebars.call(view.lookup("postClass")),
      moduleClass: Spacebars.call("post-module"),
      wrapperId: Spacebars.call(view.lookup("_id")),
      moduleData: Spacebars.call(view.lookup("post"))
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("modules"));
  });
}));
