
Template.__checkName("post_author");
Template["post_author"] = new Template("Template.post_author", (function() {
  var view = this;
  return HTML.DIV({
    "class": function() {
      return Spacebars.mustache(view.lookup("moduleClass"));
    }
  }, "\n    ", HTML.A({
    "class": "post-author",
    href: function() {
      return Spacebars.mustache(view.lookup("getProfileUrl"), Spacebars.dot(view.lookup("item"), "userId"));
    }
  }, Blaze.View("lookup:displayName", function() {
    return Spacebars.mustache(view.lookup("displayName"));
  })), "\n  ");
}));
