
Template.__checkName("post_rank");
Template["post_rank"] = new Template("Template.post_rank", (function() {
  var view = this;
  return HTML.DIV({
    "class": function() {
      return [ "post-rank ", Spacebars.mustache(view.lookup("moduleClass")) ];
    }
  }, "\n    ", HTML.DIV({
    "class": "post-rank-inner",
    "aria-live": "off"
  }, "\n      ", HTML.SPAN(Blaze.View("lookup:oneBasedRank", function() {
    return Spacebars.mustache(view.lookup("oneBasedRank"));
  })), "\n    "), "\n  ");
}));
