
Template.__checkName("main_posts_list");
Template["main_posts_list"] = new Template("Template.main_posts_list", (function() {
  var view = this;
  return HTML.DIV({
    "class": "main-posts-list"
  }, "\n    ", Blaze._TemplateWith(function() {
    return {
      zone: Spacebars.call("postsListTop")
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("modules"));
  }), "\n    ", Blaze.If(function() {
    return Spacebars.call(view.lookup("customTemplate"));
  }, function() {
    return [ "\n      ", Blaze._TemplateWith(function() {
      return {
        template: Spacebars.call(view.lookup("customTemplate")),
        data: Spacebars.call(view.lookup("arguments"))
      };
    }, function() {
      return Spacebars.include(function() {
        return Spacebars.call(Template.__dynamic);
      });
    }), "\n    " ];
  }, function() {
    return [ "\n      ", Blaze._TemplateWith(function() {
      return Spacebars.call(view.lookup("arguments"));
    }, function() {
      return Spacebars.include(view.lookupTemplate("posts_list_controller"));
    }), "\n    " ];
  }), "\n    ", Blaze._TemplateWith(function() {
    return {
      zone: Spacebars.call("postsListBottom")
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("modules"));
  }), "\n  ");
}));
