
Template.__checkName("post_actions");
Template["post_actions"] = new Template("Template.post_actions", (function() {
  var view = this;
  return HTML.DIV({
    "class": function() {
      return [ "post-actions ", Spacebars.mustache(view.lookup("moduleClass")) ];
    }
  }, "\n    ", HTML.A({
    "class": "toggle-actions-link mobile-only",
    href: "#"
  }, "\n      ", Blaze.View("lookup:icon", function() {
    return Spacebars.makeRaw(Spacebars.mustache(view.lookup("icon"), "more", "icon-circle"));
  }), "\n    "), "\n  ");
}));
