
Template.__checkName("views_menu");
Template["views_menu"] = new Template("Template.views_menu", (function() {
  var view = this;
  return HTML.DIV({
    "class": function() {
      return [ "views-menu-wrapper ", Spacebars.mustache(view.lookup("moduleClass")) ];
    }
  }, "\n    ", Blaze._TemplateWith(function() {
    return {
      menuName: Spacebars.call("view"),
      menuLabel: Spacebars.call(view.lookup("menuLabel")),
      menuItems: Spacebars.call(view.lookup("menuItems")),
      menuType: Spacebars.call("list")
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("menuComponent"));
  }), "\n  ");
}));
