
Template.__checkName("posts_list_compact");
Template["posts_list_compact"] = new Template("Template.posts_list_compact", (function() {
  var view = this;
  return HTML.TABLE("\n    ", HTML.THEAD("\n      ", HTML.TR("\n        ", HTML.TD("Post"), "\n        ", HTML.TD(Blaze.View("lookup:fieldLabel", function() {
    return Spacebars.mustache(view.lookup("fieldLabel"));
  })), "\n      "), "\n    "), "\n    ", Blaze.Each(function() {
    return Spacebars.call(view.lookup("postsCursor"));
  }, function() {
    return [ "\n      ", HTML.TR("\n        ", HTML.TD(HTML.A({
      href: function() {
        return Spacebars.mustache(view.lookup("pathFor"), "postPage", Spacebars.kw({
          _id: view.lookup("_id")
        }));
      }
    }, Blaze.View("lookup:title", function() {
      return Spacebars.mustache(view.lookup("title"));
    }))), "\n        ", HTML.TD(Blaze.View("lookup:fieldValue", function() {
      return Spacebars.mustache(view.lookup("fieldValue"));
    })), "\n      "), "\n    " ];
  }), "\n    ", Blaze.If(function() {
    return Spacebars.call(view.lookup("hasMorePosts"));
  }, function() {
    return [ "\n      ", HTML.TR("\n        ", HTML.TD({
      colspan: "2"
    }, "\n          ", HTML.A({
      "class": "downvoted-more more-button grid-module",
      href: "#"
    }, HTML.SPAN(Blaze.View("lookup:_", function() {
      return Spacebars.mustache(view.lookup("_"), "load_more");
    }))), "\n        "), "\n      "), "\n    " ];
  }), "\n  ");
}));
