
Template.__checkName("post_edit");
Template["post_edit"] = new Template("Template.post_edit", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.lookup("ready"));
  }, function() {
    return [ "\n    ", HTML.DIV({
      "class": "form-page post-edit"
    }, "\n      ", Blaze._TemplateWith(function() {
      return {
        check: Spacebars.call("edit"),
        doc: Spacebars.call(view.lookup("post")),
        message: Spacebars.call("sorry_you_cannot_edit_this_post")
      };
    }, function() {
      return Spacebars.include(view.lookupTemplate("checker"), function() {
        return [ "\n        ", HTML.DIV({
          "class": "grid grid-module"
        }, "\n          ", Blaze._TemplateWith(function() {
          return {
            collection: Spacebars.call("Posts"),
            doc: Spacebars.call(view.lookup("post")),
            id: Spacebars.call("editPostForm"),
            template: Spacebars.call("bootstrap3-horizontal"),
            "label-class": Spacebars.call("control-label"),
            "input-col-class": Spacebars.call("controls"),
            type: Spacebars.call("method-update"),
            meteormethod: Spacebars.call("editPost"),
            fields: Spacebars.call(view.lookup("postFields"))
          };
        }, function() {
          return Spacebars.include(view.lookupTemplate("quickForm"));
        }), "\n        "), "\n      " ];
      });
    }), "\n    "), "\n  " ];
  }, function() {
    return [ "\n    ", Spacebars.include(view.lookupTemplate("loading")), "\n  " ];
  });
}));
