
Template.__checkName("post_discuss");
Template["post_discuss"] = new Template("Template.post_discuss", (function() {
  var view = this;
  return HTML.DIV({
    "class": function() {
      return [ "post-discuss ", Spacebars.mustache(view.lookup("moduleClass")) ];
    }
  }, "\n    ", HTML.A({
    "class": "discuss-link go-to-comments action",
    href: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "getPageUrl"));
    },
    title: function() {
      return Spacebars.mustache(view.lookup("_"), "discuss");
    }
  }, "\n      ", Blaze.View("lookup:icon", function() {
    return Spacebars.makeRaw(Spacebars.mustache(view.lookup("icon"), "comment"));
  }), "\n      ", HTML.SPAN({
    "class": "action-count"
  }, Blaze.View("lookup:commentCount", function() {
    return Spacebars.mustache(view.lookup("commentCount"));
  })), "\n      ", HTML.SPAN({
    "class": "sr-only"
  }, " ", Blaze.View("lookup:_", function() {
    return Spacebars.mustache(view.lookup("_"), "comments");
  })), "\n    "), "\n  ");
}));
