
Template.__checkName("post_avatars");
Template["post_avatars"] = new Template("Template.post_avatars", (function() {
  var view = this;
  return HTML.DIV({
    "class": function() {
      return [ "post-avatars ", Spacebars.mustache(view.lookup("moduleClass")) ];
    },
    "aria-hidden": "true",
    "aria-live": "off"
  }, "\n    ", HTML.A({
    href: function() {
      return Spacebars.mustache(view.lookup("getProfileUrl"), view.lookup("userId"));
    },
    title: function() {
      return Spacebars.mustache(view.lookup("getDisplayName"), view.lookup("userId"));
    },
    "class": "avatar-link avatar-small author-avatar"
  }, "\n      ", Blaze._TemplateWith(function() {
    return {
      userId: Spacebars.call(view.lookup("userId")),
      shape: Spacebars.call("circle")
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("avatar"));
  }), "\n    "), "\n    ", Blaze.If(function() {
    return Spacebars.call(view.lookup("commenters"));
  }, function() {
    return [ "\n      ", HTML.DIV({
      "class": "post-commenters"
    }, "\n      ", Blaze.Each(function() {
      return Spacebars.call(view.lookup("commenters"));
    }, function() {
      return [ "\n        ", HTML.A({
        href: function() {
          return Spacebars.mustache(view.lookup("getProfileUrl"), view.lookup("."));
        },
        title: function() {
          return Spacebars.mustache(view.lookup("getDisplayName"), view.lookup("."));
        },
        "class": "avatar-link avatar-small commenter-avatar"
      }, "\n          ", Blaze._TemplateWith(function() {
        return {
          userId: Spacebars.call(view.lookup(".")),
          shape: Spacebars.call("circle")
        };
      }, function() {
        return Spacebars.include(view.lookupTemplate("avatar"));
      }), "\n        "), "\n      " ];
    }), "\n      "), "\n    " ];
  }), "\n  ");
}));
